export const asyncIsLoggedIn = () => {

  const opts = {
    method: 'POST',
    credentials: 'include',
    mode: 'cors'
  }

  return fetch(`${process.env.REACT_APP_API_URL}/auth/refresh`, opts)
    .then(response => {
      return response.status === 200;
    })
    .catch(() => {
      return false;
    });
  
}
