import { useState } from 'react';
import { Link } from 'react-router-dom';
import { HashLink } from 'react-router-hash-link';
import { 
  GlobalOutlined, 
  HomeOutlined, 
  InfoCircleOutlined,
  MenuOutlined, 
  ReadOutlined, 
  SearchOutlined,
  TeamOutlined
} from '@ant-design/icons';

const Navbar = props => {

  // Responsive menu
  const [ menuOpen, setMenuOpen ] = useState(false);

  const style = props.noImage ? {} : { backgroundImage: 'url("/images/logo_navbar_petrol.png")' };

  return (
    <nav 
      className={menuOpen ? "content-nav menu-open" : "content-nav"}
      style={style}>
      <div className="mobile-only">
        <button onClick={() => setMenuOpen(!menuOpen)}>
          <MenuOutlined />
        </button>      
      </div>
      
      <div className="nav-items">
        <HashLink className="nav-item-wrapper" to="/#map">
          <div className="nav-item">
            <GlobalOutlined />
            <span>Map</span>
          </div>
        </HashLink>

        <HashLink className="nav-item-wrapper" to="/#team">
          <div className="nav-item">
            <TeamOutlined />
            <span>Team</span>
          </div>
        </HashLink>

        <Link className="nav-item-wrapper" to="/project">
          <div className="nav-item">
            <InfoCircleOutlined />
            <span>Project</span>
          </div>
        </Link>

        <Link className="nav-item-wrapper" to="/search">
          <div className="nav-item">
            <SearchOutlined />
            <span>Database</span>
          </div>
        </Link>

        <Link className="nav-item-wrapper" to="/bibliography">
          <div className="nav-item">
            <ReadOutlined />
            <span>Bibliography</span>
          </div>
        </Link>

        <Link className="nav-item-wrapper" to="/">
          <div className="nav-item">
            <HomeOutlined />
            <span>Home</span>
          </div>
        </Link>
      </div>
    </nav>
  )

}

export default Navbar;