import { useEffect, useState } from 'react';
import { asyncIsLoggedIn } from '../../loggedIn';
import ExportFullCSV from './ExportFullCSV';
import ExportRestrictedCSV from './ExportRestrictedCSV';
import ExportFullJSON from './ExportFullJSON';

const Exports = props => {

  const [ isLoggedIn, setIsLoggedIn ] = useState(false);

  useEffect(() => {
    asyncIsLoggedIn()
      .then(result => { 
        setIsLoggedIn(result)
      });
  }, []);


  return (
    <div className="export">
      {isLoggedIn ? 
        <>
          <ExportFullJSON />
          <ExportFullCSV />
          <ExportRestrictedCSV isLoggedIn />
        </>
          
        :

        <ExportRestrictedCSV />
      }
    </div>
  )  



}

export default Exports;