import { Button } from 'antd'
import { DownloadOutlined } from "@ant-design/icons";

const URL = `${process.env.REACT_APP_API_URL}/items/house?fields=*,*.*,*.*.id,*.*.title,*.*.description`;

const buildURI = id => {
  const port = window.location.port ? `:${window.location.port}` : '';
  return `${window.location.protocol}//${window.location.hostname}${port}/house/${id}`;
}

const buildFilename = () => {
  const pad = num => num < 10 ? '0' + num : num;

  const d = new Date();

  const date = pad(d.getDate());
  const month = pad(d.getMonth() + 1);
  const year = d.getFullYear();

  return `tetrastylon-full-${year}-${month}-${date}.csv`;
}

const toCSV = data => {
  let csv = 
    'ID,URI,House Name,City,User Created,Date Created,User Updated,' +
    'Date Updated,House Dimensions,Cistern,Number of Rooms,Representation Room Typology,' +
    'Situation of Entrance,Other Uncovere Spaces,HouseDescription,Bibliography,' +
    'Comments,Longitude,Latitude,Complete Plan,Building Phases,Impluvium Extension,' +
    'Dating of Phases,Atrium Extension,Atrium Typology,Construction Date,Construction Date Numeric,' +
    'Abandon Date,Abandon Date Numeric,Typology,Distribution Area,Plans of the House,Images,' +
    'Interesting Links\n';

  data.forEach(h => {
    const cells = [
      h.id,
      buildURI(h.id),
      h.house_name,
      h.city,
      h.user_created.first_name + ' ' + h.user_created.last_name,
      h.date_created,
      h.user_updated.first_name + ' ' + h.user_updated.last_name,
      h.date_updated,
      h.house_dimensions,
      h.cistern,
      h.number_of_rooms,
      h.representation_room_typology,
      h.situtation_of_entrance,
      h.other_uncovered_spaces,
      h.house_description,
      h.bibliography,
      h.comments,
      h.longitude,
      h.latitude,
      h.complete_plan,
      h.building_phases,
      h.impluvium_extension,
      h.dating_of_phases,
      h.atrium_extension,
      h.atrium_typology,
      h.construction_date,
      h.construction_date_numeric,
      h.abandon_date,
      h.abandon_date_numeric,
      h.typology,
      h.distribution_area,
      h.plans_of_the_house?.length || 0,
      h.images?.length || 0,
      h.interesting_links && h.interesting_links.map(l => l.url)
    ]

    cells.forEach(cell => {
      const value = cell || '';
      csv += `"${value.toString().replaceAll('"', '""')}",`;
    });

    csv += '\n';
  });

  return csv;
}

const ExportFullCSV = props => {

  const onExport = () => fetch(URL)
    .then(response => response.json())
    .then(data => {
      const csv = toCSV(data.data);

      const a = document.createElement('a');
      a.href = 'data:text/csv;charset=utf-8,' + encodeURI(csv);
      a.target = '_blank';
      a.download = buildFilename();
      a.click();    
    });

  return (
    <Button 
      type="primary"
      onClick={onExport}>
      
      <DownloadOutlined /> Export Full CSV 
    </Button>
  )

}

export default ExportFullCSV;