import { Button } from 'antd'
import { DownloadOutlined } from '@ant-design/icons';

const URL = `${process.env.REACT_APP_API_URL}/items/house?fields=*,*.*,*.*.id,*.*.title,*.*.description`;

const buildFilename = () => {
  const pad = num => num < 10 ? '0' + num : num;

  const d = new Date();

  const date = pad(d.getDate());
  const month = pad(d.getMonth() + 1);
  const year = d.getFullYear();

  return `tetrastylon-full-${year}-${month}-${date}.json`;
}

const ExportFullJSON = props => {

  const onExport = () => fetch(URL)
    .then(response => response.json())
    .then(data => {
      const a = document.createElement('a');
      a.href = 'data:application/json;charset=utf-8,' + encodeURI(JSON.stringify(data));
      a.target = '_blank';
      a.download = buildFilename();
      a.click();
    });

  return (
    <Button 
      type="primary"
      onClick={onExport}>

      <DownloadOutlined /> Export JSON
    </Button>
  )

}

export default ExportFullJSON;

