import { useEffect } from 'react';
import { Button } from 'antd'
import { DownloadOutlined } from '@ant-design/icons';
import { gql, useLazyQuery } from '@apollo/client';

const buildURI = id => {
  const port = window.location.port ? `:${window.location.port}` : '';
  return `${window.location.protocol}//${window.location.hostname}${port}/house/${id}`;
}

const buildFilename = () => {
  const pad = num => num < 10 ? '0' + num : num;

  const d = new Date();

  const date = pad(d.getDate());
  const month = pad(d.getMonth() + 1);
  const year = d.getFullYear();

  return `tetrastylon-public-${year}-${month}-${date}.csv`;
}

const toCSV = data => {
  let csv = 'House Name,City,URI,Lon,Lat,Constructed,Abandoned\n';

  const fields = 
    [ 'house_name', 'city', 'id', 'longitude', 'latitude', 'construction_date', 'abandon_date' ];

  data.forEach(h => {
    const encode = field => {
      // Translate ID to URI
      const value = (field === 'id') ?
        buildURI(h[field]) : h[field];

        // Quote cell + escape double quotes
      return `"${value.toString().replaceAll('"', '""')}"`
    }

    csv += fields.map(encode) + '\n';
  });

  return csv;
}

const QUERY = gql`
  query {
    items {
      house(sort: "city", limit: 1000) {
        id,
        house_name,
        city,
        latitude,
        longitude,
        construction_date,
        abandon_date
      }
    }
  }`

const ExportRestrictedCSV = props => {

  const [ getData, { loading, data } ] = useLazyQuery(QUERY);

  useEffect(() => {
    if (data) {
      // Compile CSV
      const csv = toCSV(data.items.house);

      // Trigger download
      const a = document.createElement('a');
      a.href = 'data:text/csv;charset=utf-8,' + encodeURI(csv);
      a.target = '_blank';
      a.download = buildFilename();
      a.click();
    }
  }, [ data ]);

  return (
    <Button
      disabled={loading}
      onClick={getData}>

      <DownloadOutlined /> 
      {props.isLoggedIn ? <span>Export Limited CSV</span> : <span>Export CSV</span>}
    </Button>
  )

}

export default ExportRestrictedCSV;



